import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	
	data() {
		return {			
			list1:[
				{user_id: 13072,
				  avatar: 'https://detion.oss-cn-beijing.aliyuncs.com/346F5221-E333-44DC-A525-112416BF2535.jpg',
				  nickname: 'dsadsada',
				  company: null,
				  job: null,
				  type: '2',
				  no: 10011,
				  created_at: 1223,
				  status: 4}
			],				
			page1:1,
			loading1:false,
			count:0,
			allow0:false,
			showTitle:false
			
		}
	},
	onLoad() {
		this.getFan()
	},
	// 上拉加载更多
	onReachBottom(){
		if(this.allow0){
			this.allow0 = false
			this.loading1 = true
			this.page1++
			this.getFan()
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.page1 = 1
		this.getFan()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000)
	},
	methods: {
		
		getFan(){
			this.$api.my_recommend({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				page:this.page1
			}).then(res=>{
				// console.log(res)
				if (res.data.code === '200') {
					this.count = res.data.data.count
					if(this.page1 === 1){
						this.list1 = res.data.data.list
						if(res.data.data.list.length>=20){
							this.allow0 = true
							
						}else{
							this.showTitle = true
							this.allow0 = false
						}														
					}else{
						this.list1 = this.list1.concat(res.data.data.list)
						if(res.data.data.list.length>=20){
							this.allow0 = true
							
						}else{
							this.showTitle = true
							this.allow0 = false
						}	
					}
					this.loading1 = false

				}else if (res.data.code === '1010') {
				  uni.clearStorageSync()
				  uni.showToast({
				      title: '登录过期，请重新登录',
				      duration: 2000,
				  	icon:'none'
				  });
				  
				  uni.reLaunch({
				  	url: '../../pages/login/login'
				  })
				}
			})
		},
		
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		// 点击关注 取消等
		vote (item) {
		  if (item.status === 1 || item.status === 2) { // 如果是是互相关注，或者是已关注，点击即是取消关注，显示关注
			Dialog.confirm({
			  title: '提示',
			  message: '确定取消关注吗？'
			}).then(() => {
				this.follow(item)
			}).catch(() => {
			  // on cancel
			});
		    
		  } else if (item.status === 3) { // 如果是被关注，点击关注显示互相关注
		    this.follow(item)
		  } else if (item.status === 4) { // 如果是都未关注，显示已关注
		    this.follow(item)
		  }
		},
		follow (item) {
		  this.$api.NewsFollow({
		    token: uni.getStorageSync("token"),
		    user_id: uni.getStorageSync("userId"),
		    follow_id: item.user_id
		  }).then(res => {
		    item.status = res.data.data
		    Toast(res.data.msg)
			
		  })
		}
	}
}
